/**
* Sfera.Net handles browser URL related tasks such as checking host names, domain names and query string manipulation.
*
* @class Sfera.Net
* @constructor
* @param {Sfera.Client} client - A reference to the currently running client.
*/
Sfera.Net = function (client) {

    this.client = client;

    var req = new Sfera.Net.Request();
    req.onLoaded = onReqLoaded;
    req.onError = onReqError;

    var webSocket;
    var wsConnected = false;

    var self = this;
    var wsBaseUrl = "ws://localhost:8080/api/websocket";
    var httpBaseUrl = "/";

    this.subscribeId = (new Date()).getTime();

    function openSocket() {
    	console.log("opening socket on "+wsBaseUrl);
        // Ensures only one connection is open at a time
        if (webSocket !== undefined && webSocket.readyState !== WebSocket.CLOSED) {
            writeResponse("WebSocket is already opened.");
            return;
        }
        // Create a new instance of the websocket

        webSocket = new WebSocket(wsBaseUrl);

        webSocket.onopen = function(event) {
            // For reasons I can't determine, onopen gets called twice
            // and the first time event.data is undefined.
            // Leave a comment if you know the answer.
            if (event.data === undefined)
                return;

            console.log("websocket open, sending subscribe");

            writeResponse(event.data);

            //self.wsSend("hello");
            self.wsSend(Sfera.urls.get("subscribe"));
        };

        webSocket.onmessage = function(event) {
            if (!wsConnected) {
                wsConnected = true;
                self.wsSend(Sfera.urls.get("subscribe"));
            }
            writeResponse(event.data);
        };

        webSocket.onclose = function(event) {
            writeResponse("Connection closed");
            openSocket(); // reopen
        };
    }

    /**
     * Sends the value of the text input to the server
     */
    this.wsOpen = function () {
    	openSocket();
    }
    this.wsSend = function (txt) {
        console.log("sending "+txt);
        webSocket.send(txt);
    }
    this.wsClose = function () {
        webSocket.close();
    }

    function writeResponse(text) {
    	console.log("websocket: "+text);
		var e = document.getElementById("output");
		e.innerHTML += "webSocket response:<br><textarea style='width:500px; height:200px'>"+text+"</textarea><br><br>";
    }

    ///////////

	var started = false; // connected: ongoing state requests

	var cSync = ""; // currently synchronizing resource

	var self = this;

	this.stateTs = -1;
	this.subscribed = false; // change this

	// local timestamps, to check required updates
	this.localTs = {
		"dictionary":-1,
		"index":-1
	};

	this.remoteTs = {
		"dictionary":-1,
		"index":-1
	};

	// get current timestamp
	function getTimestamp() {
		return (new Date()).getTime();
	}

	// boot
	this.boot = function () {
		this.sync();
	}; // boot()

	// sync, if necessary
	this.sync = function () {
		for (var s in this.localTs) {
			if (this.localTs[s] == -1) { // || this.localTs[s] < this.remoteTs[s]) {
				cSync = s;
				req.open(httpBaseUrl+Sfera.urls.get(s),20);
				return; // one resource per time
			}
		}

        // use websockets?
        if (true) {
            openSocket();
            return;
        }

		if (!this.subscribed) {
			cSync = "subscribe";

			req.open(urls.get("subscribe"));
			return;
		}

		cSync = "state";
		req.open(httpBaseUrl+urls.get("state"));
	};

	function onReqLoaded() {
		console.log(cSync+" loaded");
		var e = document.getElementById("output");
		e.innerHTML += cSync+" loaded:<br><textarea style='width:500px; height:200px'>"+req.getResponseText()+"</textarea><br><br>";

		if (self.localTs[cSync] != null)
			self.localTs[cSync] = getTimestamp();

		var state;

		switch (cSync) {
		case "dictionary":
			console.log("loaded dictionary");
            client.onUpdateDictionary(req.getResponseXML())
			break;
		case "index":
			console.log("loaded index");
            client.onUpdateIndex(req.getResponseXML());
			break;

		case "subscribe":
			self.subscribed = true;

		case "state":
			state = JSON.parse(req.getResponseText());
			if (state.id)
				config.clientId = state.id;
			if (state.timestamp)
				self.stateTs = state.timestamp;
			break;
		}

		self.sync();
	}
	function onReqError() {
		console.log("error");
		var e = document.getElementById("output");
		e.innerHTML += "<br><br>Error.<br><br>";
	}


    /**
    * Returns the hostname given by the browser.
    *
    * @method Sfera.Net#getHostName
    * @return {string}
    */
    this.getHostName = function () {

        if (window.location && window.location.hostname) {
            return window.location.hostname;
        }

        return null;

    };


    this.sendCommand = function (id, command) {
        if (true) {
            this.wsSend(Sfera.urls.get("command")+"?"+command);
        } else {

        }
    };

};
